.App {
  text-align: center;
}

@font-face {
  font-family: "Laura Barbara";
  src: url("./assets/font1.ttf");
}

@font-face {
  font-family: "나눔손글씨 성실체";
  src: url("./assets/font2.ttf");
}

#main-text {
  color: #c9c3d6;
  font-size: 3rem;
  -webkit-text-stroke: 0.05rem #5c557f;
  font-family: 'Laura Barbara';
}

#main-image {
  max-width: 100%;
}

.paragraph {
  margin-bottom: 1.5rem;
}

.text {
  color: black;
  font-size: 1.5rem !important;
  /* -webkit-text-stroke: 0.05rem #5c557f; */
  font-family: '나눔손글씨 성실체' !important;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}